import React, { useState, useEffect } from "react"

import Navbar from "./components/Navbar"
import TodoForm from "./components/TodoForm"
import Todos from "./components/Todos"

function App(){
	let prevData = localStorage.getItem('rtdata')
	prevData = prevData && JSON.parse(prevData)
	
	let [todos, setTodos] = useState(prevData || [])
	
	useEffect(() => {
		localStorage.setItem('rtdata', JSON.stringify(todos))
	}, [todos])
	
	const updateTodo = i => {
		let _todos = [...todos]
		_todos[i].completed = !_todos[i].completed
		setTodos(_todos)
	}
	
	const addTodo = todo => {
		if(!todo || !todo.trim()) return
		setTodos([{ title: todo, completed: false}, ...todos])
	}
	
	
	
	return (
		<div id="app">
			<Navbar />
			<TodoForm addTodo={addTodo} />
			{todos.length > 0 && <p className="clear"><span onClick={() => setTodos([])}>clear</span></p>}
			<Todos todos={todos} updateTodo={updateTodo} />
		</div>
	)
}

export default App