import React from "react"

function Todos({ todos, updateTodo }){
	let content
	if(todos.length > 0){
		content = todos.map((todo, index) => <div className="todo" key={index} onClick={() => updateTodo(index)}>{ todo.title } <sub style={{color: todo.completed ? "#03A9F4" : "#4CAF50"}}>- { todo.completed ? "completed" : "pending" }</sub></div>)
	}else{
		content = <p className="msg">Jobless ...</p>
	}
	return (
		<div class="todos">
			{ content }
		</div>
	)
}

export default Todos


